html {
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0;
}

img {
  max-width: 100%;
}

body {
  overflow: hidden;
  color: #ffffff;
  font-family: "Geometria", sans-serif;
  font-weight: 400;
  font-size: 17px;
  line-height: 1.5;
  letter-spacing: -0.02em;
  background-color: #ffffff;
}

.container {
  max-width: 1400px;
  padding: 0 20px;
  margin: 0 auto;
}

.title {
  color: #ffffff;
  font-weight: 700;
  font-size: 100px;
  line-height: 1;
  letter-spacing: -0.02em;
}

@media (max-width: 1830px) {
  .title {
    font-size: 75px;
  }
}

@media (max-width: 1300px) {
  .title {
    font-size: 60px;
  }
}

@media (max-width: 991px) {
  .title {
    font-size: 50px;
  }
}

@media (max-width: 767px) {
  .title {
    font-size: 40px;
  }
}

@media (max-width: 567px) {
  .title {
    font-size: 28px;
  }
}

.title-2 {
  color: #ffffff;
  font-weight: 700;
  font-size: 60px;
  line-height: 1;
  letter-spacing: -0.03em;
}

@media (max-width: 1300px) {
  .title-2 {
    font-size: 50px;
  }
}

@media (max-width: 991px) {
  .title-2 {
    font-size: 40px;
  }
}

@media (max-width: 567px) {
  .title-2 {
    font-size: 28px;
  }
}

.title-3 {
  margin: 0;
  font-weight: 700;
  color: #fff;
  font-size: 40px;
  line-height: 1;
}

.vissually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transform: scale(0);
  z-index: 91;
  background-color: #000;
  opacity: 0;
  transition: opacity 0.5s;
}

.overlay.active {
  opacity: 0.7;
  transform: scale(1);
}

.s-arrow-right {
  width: 28px;
  height: 16px;
}

.s-lips {
  max-height: 70px;
  max-width: 70px;
}

.s-map-marker {
  width: 11px;
  height: 14px;
}

.s-close-icon {
  width: 20px;
  height: 20px;
}

.tm-i-instagram:hover circle,
.tm-i-vk:hover circle {
  fill: #343779;
}

.tm-i-instagram:hover path,
.tm-i-vk:hover path {
  fill: #fff;
}

.tm-i-instagram circle,
.tm-i-vk circle {
  stroke: rgba(52, 55, 121, 0.12);
  fill: transparent;
  transition: fill 0.3s;
}

.tm-i-instagram path,
.tm-i-vk path {
  fill: #343779;
  transition: all 0.3s;
}

.tm-i-instagram--white circle,
.tm-i-vk--white circle {
  stroke: rgba(255, 255, 255, 0.12);
  fill: transparent;
}

.tm-i-instagram--white path,
.tm-i-vk--white path {
  fill: #fff;
}

.tm-i-instagram--white:hover circle,
.tm-i-vk--white:hover circle {
  fill: #fff;
}

.tm-i-instagram--white:hover path,
.tm-i-vk--white:hover path {
  fill: #343779;
}

.tk-a-scale {
  transform: scale(0);
  transition: transform 0.8s;
}

.tk-a-scale.tk-anim-active {
  transform: scale(1);
}

.tk-a-rotate {
  transform: rotateX(90deg);
  opacity: 0;
  transition: transform 0.8s, opacity 1s;
}

.tk-a-rotate.tk-anim-active {
  transform: rotateX(0deg);
  opacity: 1;
}

.tk-a-translate-b {
  transform: translateY(100px);
  opacity: 0;
  transition: transform 0.8s, opacity 1s;
}

.tk-a-translate-b.tk-anim-active {
  transform: translate(0);
  opacity: 1;
}

.tk-a-translate-l {
  transform: translateX(-200px);
  opacity: 0;
  transition: transform 0.8s, opacity 1s;
}

.tk-a-translate-l.tk-anim-active {
  transform: translate(0);
  opacity: 1;
}

.tk-a-translate-r {
  transform: translateX(200px);
  opacity: 0;
  transition: transform 0.8s, opacity 1s;
}

.tk-a-translate-r.tk-anim-active {
  transform: translate(0);
  opacity: 1;
}

.tk-a-opacity {
  opacity: 0;
  transition: opacity 0.5s;
}

.tk-a-opacity.tk-anim-active {
  opacity: 1;
}

.tm-field {
  position: relative;
  display: block;
}

.tm-field__input {
  display: block;
  width: 100%;
  padding: 20px 10px 20px 0px;
  font-weight: 500;
  font-size: 17px;
  color: #343779;
  border: none;
  border-bottom: 1px solid #343779;
  outline: transparent;
  transition: border-color 0.3s;
}

@media (max-width: 567px) {
  .tm-field__input {
    padding: 10px 10px 10px 0px;
    font-size: 11px;
  }
}

.tm-field__input:focus {
  border-color: #f8b750;
}

.tm-field__input::placeholder {
  font-weight: 400;
  color: #343779;
}

.btn {
  position: relative;
  padding: 24px 100px 24px 30px;
  color: #343779;
  font-weight: 500;
  font-size: 15px;
  text-transform: uppercase;
  text-align: left;
  background-color: #f8b750;
  border: none;
  cursor: pointer;
}

@media (max-width: 1300px) {
  .btn {
    padding-right: 90px;
  }
}

@media (max-width: 991px) {
  .btn {
    padding: 20px 80px 20px 20px;
    font-size: 12px;
  }
}

.btn--transparent {
  background-color: transparent;
  border: 2px solid #343779;
  transition: color 0.3s, background-color 0.3s;
}

.btn--transparent:hover {
  color: #fff;
  background-color: #343779;
}

.btn--transparent:hover::after {
  background-color: #fff;
}

@media (min-width: 767px) {
  .btn--transparent:hover::after {
    transform: translateY(-50%) scaleX(1.2);
  }
}

.btn--transparent .btn__bg {
  display: none;
}

.btn:disabled {
  background-color: grey;
  color: #fff;
  cursor: auto;
  pointer-events: none;
}

.btn:disabled::after {
  background-color: #fff;
}

.btn::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 30px;
  z-index: 15;
  transform-origin: left;
  width: 28px;
  height: 28px;
  mask-image: url(../img/svg/sprite1.svg#arrow-right);
  background-color: #343779;
  background-size: scale;
  background-repeat: no-repeat;
  transition: background-color 0.5s, transform 0.3s;
}

@media (max-width: 991px) {
  .btn::after {
    width: 21px;
    height: 21px;
    right: 20px;
  }
}

.btn:hover .btn__wave {
  transform: translate(0, 0);
}

.btn:hover .btn__wave::before, .btn:hover .btn__wave::after {
  -webkit-animation-play-state: running !important;
  animation-play-state: running !important;
}

.btn:hover .btn__text {
  animation-name: buttonText;
}

.btn--white {
  border-color: #fff;
  color: #fff;
}

.btn--white:hover {
  background-color: #fff;
  color: #343779;
}

.btn--white:hover::after {
  background-color: #343779;
}

.btn--white::after {
  background-color: #fff;
}

.btn__bg {
  overflow: hidden;
  z-index: 1;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  line-height: normal;
  font-size: 0;
  pointer-events: none;
}

.btn__wave {
  display: block;
  position: relative;
  height: 80%;
  top: 20%;
  line-height: normal;
  -webkit-transform: translate(0, 150%);
  transform: translate(0, 150%);
  font-size: 0;
  transition: ease-out 0.3s;
}

.btn__wave::before, .btn__wave::after {
  content: "";
  position: absolute;
  line-height: normal;
  left: 50%;
  width: 312.5%;
  padding-top: 312.5%;
  margin-left: -156%;
  -webkit-transform-origin: 50% 48%;
  transform-origin: 50% 48%;
  border-radius: 43%;
  z-index: 1;
  top: 0;
  -webkit-animation: wave 3s infinite linear;
  animation: wave 3s infinite linear;
  -webkit-animation-play-state: paused !important;
  animation-play-state: paused !important;
}

.btn__wave::before {
  animation: wave 4s infinite linear;
  z-index: 2;
  background: #ffcb7a;
  margin-top: 15%;
}

.btn__wave::after {
  animation: wave 5s infinite linear;
  background: #ffc162;
  margin-top: 13%;
}

.btn__wave--transparent::before {
  background-color: #51e2f2;
  opacity: 1;
}

.btn__wave--transparent::after {
  background-color: #51e2f2;
  opacity: 0.7;
}

.btn__text {
  display: inline-block;
  line-height: normal;
  z-index: 15;
  position: relative;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-duration: 1s;
}

@keyframes wave {
  from {
    -webkit-transform: rotate(0) translateZ(-1px);
    transform: rotate(0) translateZ(-1px);
  }
  to {
    -webkit-transform: rotate(360deg) translateZ(-1px);
    transform: rotate(360deg) translateZ(-1px);
  }
}

@keyframes buttonText {
  from {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  25% {
    -webkit-transform: translate(0, -5%);
    transform: translate(0, -5%);
  }
  50% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  75% {
    -webkit-transform: translate(0, 5%);
    transform: translate(0, 5%);
  }
  to {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
}

.tm-checkbox {
  position: relative;
  z-index: 10;
  display: block;
}

.tm-checkbox__input {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.tm-checkbox__input:checked + .tm-checkbox__box::after {
  transform: scale(1);
}

.tm-checkbox__input:focus + .tm-checkbox__box {
  border-color: #f8b750;
}

.tm-checkbox__input:focus + .tm-checkbox__box::after {
  background-color: #f8b750;
}

.tm-checkbox__box {
  box-sizing: border-box;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  width: 25px;
  height: 25px;
  background-color: #fff;
  border: 2px solid #343779;
  cursor: pointer;
  transition-property: border-color;
  transition-duration: 0.3s;
}

@media (max-width: 567px) {
  .tm-checkbox__box {
    width: 19px;
    height: 19px;
  }
}

.tm-checkbox__box::after {
  content: "";
  position: relative;
  left: 0;
  top: 0;
  transform: scale(0);
  width: 11px;
  height: 11px;
  background-color: #343779;
  transition-property: background-color, transform;
  transition-duration: 0.3s;
}

@media (max-width: 567px) {
  .tm-checkbox__box::after {
    width: 7px;
    height: 7px;
  }
}

